import {post} from "./http"

export default{
    Login(req){  //用户登录
        return post("/user-service/userManage/login",req)
    },
    Register(req){ //用户注册
        return post("/user-service/userManage/register",req)
    },
    getUserMes(req){ //取得用户个人信息
        return post("/user-service/userManage/getUserList",req)
    },
    checkUserName(req){ //检测用户名是否重名
        return post("/user-service/userManage/checkRepeat",req)
    },
    modifyUser(req){  //修改密码和修改信息
        return post("/user-service/userManage/updateUser",req)
    },
    removeUser(req){ //注销账户
        return post("/user-service/userManage/removeUser",req)
    },
    getWindows(req){ //取得窗口信息
        return post("/window-service/windowManage/getWindows",req)
    },
    modifyWindows(req){ //修改窗口信息
        return post("/window-service/windowManage/modifyWindow",req)
    },
    addWindows(req){ //添加窗口信息
        return post("/window-service/windowManage/addWindow",req)
    },
    deleteWindows(req){ //删除窗口信息
        return post("/window-service/windowManage/deleteWindow",req)
    },
    getWorkList(req){ //得到工作人员信息
        return post("/staff-service/staffManage/getStaffList",req)
    },
    getmaterials(req){ //得到材料信息
        return post("/materials-service/materialsManage/getMaterialsList",req)
    },
    deletematerials(req){ //删除材料
        return post("/materials-service/materialsManage/removeMaterial",req)
    },
    addmaterials(req){ //添加材料
        return post("/materials-service/materialsManage/addMaterial",req)
    },
    modifymaterials(req){ //修改材料
        return post("/materials-service/materialsManage/updateMaterial",req)
    },
    getBusiness(req){ //业务类型获取
        return post("/business-service/businessManage/getBusinesses",req)
    },
    getleaveMes(req){ //得到留言信息
        return post("/message-service/messageManage/getMessages",req)
    },
    removeLeaveMes(req){  //删除留言信息
        return post("/message-service/messageManage/removeMessage",req)
    },
    modeifyLeaveMes(req){ //修改留言信息
        return post("/message-service/messageManage/updateMessage",req)
    },
    addLeaveMes(req){ //添加留言信息
        return post("/message-service/messageManage/addMessage",req)
    },
    subscribe(req){ //预约
        return post("/queue-service/queueManage/addQueue",req)
    },
    getQueue(req){  //得到本人办理业务队列
        return post("/queue-service/queueManage/getQueueList",req)
    },
    cancelWork(req){ //撤销办理业务
        return post("/queue-service/queueManage/repealQueue",req)
    },
    passNumber(req){ //过号处理
        return post("/queue-service/queueManage/missTurnHandle",req)
    },
    dealWork(req){ //办理业务
        return post("/queue-service/queueManage/transactQueue",req)
    },
    overQueue(req){
        return post("/queue-service/queueManage/overQueue",req)
    }
}